$font-size-small: 1em;
$font-size-medium: 1.5em;
$font-size-large: 2em;

$block-background: #e0e0e0;
$blockquote-background: #f0f0f0;

body {
    font-family: Arial, sans-serif;
}

header, nav, section, footer {
    margin: 20px;
    padding: 10px;
}

h1 {
    font-size: $font-size-large;
}

h2 {
    font-size: $font-size-medium;
}

p, ul, table {
    font-size: $font-size-small;
}

img {
    width: 200px;
    height: auto;
    margin: 10px;
}

blockquote {
    font-style: italic;
    margin: 20px;
    padding: 10px;
    background-color: $blockquote-background;
}

.block {
    background-color: $block-background;
    padding: 10px;
    margin: 10px 0;
}

footer {
    text-align: center;
}
